import React from 'react'
import { Link } from 'react-router-dom';


export const CardFuegoArtificial = (props) => {

  const { title, img, description } = props
  return (
    <>
      <Link className="col mb-4" type="button" data-toggle="modal" data-target="#staticBackdrop">
        <div className="card h-100 ">
          <img src={img} className="card-img-top imgCard" alt="..." />
          <div className="card-body">
            <h5 className="card-title">{title}</h5>
            <p className="card-text">{description}</p>
          </div>
        </div>
      </Link>
      {/* <!-- Modal --> */}
      <div className="modal fade  modal-xl" id="staticBackdrop" data-backdrop="static" data-keyboard="false" tabindex="-1" aria-labelledby="staticBackdropLabel" aria-hidden="true">
        <div className="modal-dialog">
          <div className="modal-content">
            <div className="modal-header">
              <h5 className="modal-title" id="staticBackdropLabel">{title}</h5>
              <button type="button" className="close" data-dismiss="modal" aria-label="Close">
                <span aria-hidden="true">&times;</span>
              </button>
            </div>
            <div className="modal-body">
              <img src={img} className="card-img-top" alt="..." />

              <div className="row pt-2">
                <div className="col-3">
                  <div className="nav flex-column nav-pills" id="v-pills-tab" role="tablist" aria-orientation="vertical">
                    <a className="nav-link active" id="v-pills-home-tab" data-toggle="pill" href="#v-pills-home" role="tab" aria-controls="v-pills-home" aria-selected="true">Home</a>
                    <a className="nav-link" id="v-pills-profile-tab" data-toggle="pill" href="#v-pills-profile" role="tab" aria-controls="v-pills-profile" aria-selected="false">Contacto</a>
                  </div>
                </div>
                <div class="col-9">
                  <div class="tab-content" id="v-pills-tabContent">
                    <div class="tab-pane fade show active" id="v-pills-home" role="tabpanel" aria-labelledby="v-pills-home-tab">
                      <p>Melbourne con un nuevo diseño, más moderno y atractivo, Introduce nuevos estándares de calidad en el mercado nacional.
                  </p>
                    </div>
                    <div class="tab-pane fade" id="v-pills-profile" role="tabpanel" aria-labelledby="v-pills-profile-tab">
                      <form className="pt-4">
                        <div class="form-row">
                          <div className="form-group col-md-6">
                            <input type="text" id="nombre" className="form-control" name='name'
                              maxLength='10' placeholder='Nombre Completo' />
                          </div>
                          <div className="form-group col-md-6">
                            <input type="number" className="form-control" id="cellphone" placeholder='Numero de telefono' />
                          </div>
                        </div>
                        <div className="form-group">
                          <textarea className="form-control" id="message-text" placeholder='Consulta:'></textarea>
                        </div>
                      </form>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div className="modal-footer">
              <button type="button" className="btn btn-secondary" data-dismiss="modal">Close</button>
              <button type="button" className="btn btn-primary">Enviar</button>
            </div>
          </div>
        </div>
      </div>

    </>
  )
}
